html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

/* Full-width navbar */
.navbar {
    width: 100%;
    height: 70px;
    background-color: #3bb19b;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0;
    padding: 0 20px;
    box-sizing: border-box;
    position: relative;
}

/* Navbar title */
.navbar h1 {
    color: white;
    font-size: 25px;
    margin: 0;
}

.surveyForm {
  display: flex;
  flex-direction: column;
  align-items: center; /* Centers the form horizontally */
  gap: 30px; /* Consistent spacing between survey questions */
  padding: 20px; /* Add some padding for better readability */
}

.surveyQuestion {
  display: flex;
  flex-direction: column; /* Keeps the label and options grouped together */
  align-items: center; /* Centers each question horizontally */
  width: 100%; /* Ensures consistent alignment across the page */
}

.surveyQuestion label {
  font-size: 18px; /* Increase font size for better readability */
   /* Makes the question text stand out */
  margin-bottom: 10px; /* Add spacing below the question text */
  text-align: center; /* Centers the question text */
}

.surveyOptions {
  display: flex; /* Ensures horizontal stacking of options */
  justify-content: center; /* Center the options horizontally */
  gap: 15px; /* Adds space between the radio buttons */
  flex-wrap: wrap; /* Allows wrapping if there are many options on smaller screens */
}

.surveyOptions label {
  font-size: 16px; /* Adjust font size for options */
  line-height: 1.5; /* Improves readability */
  display: flex;
  align-items: center; /* Aligns the radio button with the text */
}

.submitButton {
  margin-top: 20px;
  padding: 12px 24px;
  background-color: #007BFF;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.submitButton:hover {
  background-color: #0056b3; /* Add hover effect for the button */
}

.textarea {
  width: 100%;
  margin-top: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  line-height: 1.5;
}

.toggleDiffBtn {
  background-color: #ffffff; /* White background */
  color: #000000; /* Black text color */
  border: 1px solid #ccc; 
  /* Optional: Add a border for better visibility */
  padding: 5px 5px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  font-weight: bold;
}

.toggleDiffBtn:hover {
  background-color: #f0f0f0; /* Slightly darker background on hover */
  color: #000000; /* Keep text black on hover */
}

