/* Main container for content */
.main_container {
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
  max-width: 100%;
}


/* Form container for single and dual layouts */
.form_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    width: 100%;
}

/* Container for input and output (side-by-side) */
/* .textareas_container {
