    resize: both; 

} */

.textareas_container {
  display: flex;
  gap: 20px;
}

/* Submit and Save button styles */
.submit_btn {
    background-color: #333;
    color: white;
    /* width: auto; */
    width: 800px;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
    margin-top: 20px;
    text-align: center;
    align-self: center;
}

.submit_btn:hover {
    background-color: #555;
}

.upload_container {
	margin-top: 10px;
}

.file_input {
	display: block;
	margin: 10px auto;
	padding: 5px;
}

/* Container for the input area and drag-and-drop box */
.input_area {
    display: flex;
    flex-direction: row; /* Align the input and upload area side by side */
    gap: 20px; /* Space between the input and upload box */
    width: 100%;

}

/* Drag-and-drop upload box */
.upload_box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 200px; /* Fixed width for the upload box */
    height: 100px; /* Same height as the text box */
    border: 2px dashed #ccc; /* Dashed border for drag-and-drop area */
    border-radius: 4px;
    background-color: #f9f9f9;
    color: #333;
    font-size: 14px;
    cursor: pointer;
    margin-top: 50px;
    text-align: center;
    box-sizing: border-box;
    transition: background-color 0.3s ease, border-color 0.3s ease;
}

/* Hover effect for upload box */
.upload_box:hover {
    background-color: #eaeaea;
    border-color: #3bb19b;
}

/* Hidden file input */
.hidden_input {
    display: none;
}

/* Disable the submit button until input is provided */
.submit_btn:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}



