.output_box {
  /* flex: 1; */
  width: 100%;
  min-height: 400px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  margin-top: 15px;
  background-color: #f9f9f9;
  color: #333;

  box-sizing: border-box;
  resize: both;
  overflow: auto;
  white-space: pre-wrap;

  direction: ltr; /* Enforces left-to-right typing */
  text-align: left; /* Aligns text to the left */
}

.diff_container {
  display: flex; /* Use flexbox for horizontal alignment */
  gap: 20px; /* Add some spacing between the two boxes */
  margin-top: 20px; /* Add spacing from the main content */
}

.diff_box {
  flex: 1; /* Ensure both boxes take equal space */
  border: 1px solid #ccc; /* Add a border for better visual separation */
  border-radius: 8px;
  padding: 15px;
  /* background-color: #f9f9f9; */
  overflow-y: auto; /* Handle overflow if content is too long */
  max-height: 200px; /* Limit height of the boxes */
}

.diff_output {
  white-space: pre-wrap; /* Preserve line breaks */
  font-family: "Courier New", Courier, monospace; /* Use a monospaced font for diffs */
  font-size: 14px;
  line-height: 1.5;
}


.output_box span {
  display: inline;
}

.labelWrapper {
  display: flex;
  justify-content: space-between; /* Aligns label and icon */
  align-items: center;
  margin-bottom: 0px; /* Space between label and text box */
}

