/* Input and Output box side by side */
/* Equal width for both input and output boxes */
.side_by_side {
    flex: 1; 
}

/* Output box styles */
.output_box {

    width: 100%;
    min-height: 400px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    margin-top: 15px;
   
    color: #333;

    box-sizing: border-box;
    resize: both;
    overflow: auto;
    white-space: pre-wrap;

    direction: ltr;
    text-align: left; 
}


.output_box span {
    display: inline;
  }
  
  .output_box .diff-added {
    background-color: #e6ffe6; /* Light green for added text */
  }
  
  .output_box .diff-deleted {
    background-color: #ffe6e6; /* Light red for deleted text */
    text-decoration: line-through;
  }


.labelWrapper {
    display: flex;
    justify-content: space-between; /* Aligns label and icon */
    align-items: center;
    margin-bottom: 0px; /* Space between label and text box */
  }
  
/* Label Styling */
.label {
font-size: 16px;
font-weight: bold;
margin-bottom: 0px; /* Space between label and textarea */
text-align: left; /* Ensure label aligns left */
}


/* "OR" Divider Styling */
.or_divider {
margin: 90px 0;
font-size: 16px;
font-weight: bold;
text-align: center;
}
  
/* Save/Edit Button Styling */
.text_btn {
    margin-top: 10px; /* Space between the button and the text box */
    padding: 10px 20px;
    border-radius: 4px;
    font-weight: bold;
    cursor: pointer;
    font-size: 16px;
    text-align: center;
    width: fit-content; /* Only as wide as the text */
  }
  
  .save_btn, .edit_btn {
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .save_btn {
    background-color: #4caf50;
    color: white;
    border: none;
  }
  
  .save_btn:hover {
    background-color: #45a049;
  }
  
  .edit_btn {
    background-color: #2196f3;
    color: white;
    border: none;
  }
  
  .edit_btn:hover {
    background-color: #0b7dda;
  }
  
  .save_btn:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}
  
/* App Description Styling */
.description {
    margin: 20px auto;
    text-align: center;
    font-size: 16px;
    color: #333;
    max-width: 80%;
    line-height: 1.5;
  }