html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

/* Full-width navbar */
.navbar {
    width: 100%;
    height: 70px;
    background-color: #3bb19b;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0;
    padding: 0 20px;
    box-sizing: border-box;
    position: relative;
}

/* Navbar title */
.navbar h1 {
    color: white;
    font-size: 25px;
    margin: 0;
}

/* Logout button */
.white_btn {
    border: none;
    outline: none;
    padding: 12px 20px;
    background-color: white;
    border-radius: 20px;
    font-weight: bold;
    font-size: 14px;
    cursor: pointer;
}

/* Main container for content */
.main_container {
    margin: 0 auto;
    padding: 20px;
    font-family: Arial, sans-serif;
    max-width: 100%;
}

/* Form container for single and dual layouts */
.form_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    width: 100%;
}

/* Container for input and output (side-by-side) */
.textareas_container {
    display: flex;
    gap: 20px; /* Space between input and output */
    width: 100%; /* Full width of the container */
    resize: both; 

}

/* Submit and Save button styles */
.submit_btn {
    background-color: #333;
    color: white;
    /* width: auto; */
    width: 100%;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
    margin-top: 20px;
    text-align: center;
    align-self: center;
}

.submit_btn:hover {
    background-color: #555;
}

.upload_container {
	margin-top: 10px;
}

.file_input {
	display: block;
	margin: 10px auto;
	padding: 5px;
}

/* Container for the input area and drag-and-drop box */
.input_area {
    display: flex;
    flex-direction: row; /* Align the input and upload area side by side */
    gap: 20px; /* Space between the input and upload box */
    width: 100%;

}

/* Drag-and-drop upload box */
.upload_box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 300px; /* Fixed width for the upload box */
    height: 100px; /* Same height as the text box */
    border: 2px dashed #ccc; /* Dashed border for drag-and-drop area */
    border-radius: 4px;
    background-color: #f9f9f9;
    color: #333;
    font-size: 16px;
    cursor: pointer;
    margin-top: 220px;
    text-align: center;
    box-sizing: border-box;
    transition: background-color 0.3s ease, border-color 0.3s ease;
}

/* Hover effect for upload box */
.upload_box:hover {
    background-color: #eaeaea;
    border-color: #3bb19b;
}

/* Hidden file input */
.hidden_input {
    display: none;
}

/* Disable the submit button until input is provided */
.submit_btn:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

  

