/* Textarea Styling */



/* Input box full width when alone */
.textarea {
    width: 100%;
    min-height: 400px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    box-sizing: border-box;
    resize: both;
    overflow: auto;

} 

/* Input and Output box side by side */
/* Equal width for both input and output boxes */
.side_by_side {
    flex: 1; 
}

/* Output box styles */
.output_box {
    /* flex: 1; */
    width: 100%;
    min-height: 150px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;

    background-color: #f9f9f9;
    color: #333;

    box-sizing: border-box;
    resize: both;
    overflow: auto;
}

