/* Container for Input and Output Boxes */
.text_container {
    width: 100%; /* Full width */
    display: flex;
    flex-direction: column; /* Stack label and textarea vertically */
    align-items: flex-start; /* Align label and text box to the left */
    margin-bottom: 16px; /* Add spacing between text boxes */
}

/* Container for Input and Output Areas Side-by-Side */
.textareas_container {
    display: flex;
    gap: 20px; /* Space between input and output boxes */
    width: 100%; /* Full width of the container */
    align-items: stretch;
}

  
/* Label Styling */
.label {
font-size: 16px;
font-weight: bold;
margin-bottom: 8px; /* Space between label and textarea */
text-align: left; /* Ensure label aligns left */
margin-top: 40px;
}


/* "OR" Divider Styling */
.or_divider {
margin: 260px 0;
font-size: 20px;
font-weight: bold;
text-align: center;
}
  
/* Save/Edit Button Styling */
.text_btn {
    margin-top: 10px; /* Space between the button and the text box */
    padding: 10px 20px;
    border-radius: 4px;
    font-weight: bold;
    cursor: pointer;
    font-size: 14px;
    text-align: center;
    width: fit-content; /* Only as wide as the text */
  }
  
  /* Save Button Specific Styling */
  .save_btn {
    background-color: #4caf50; /* Green for Save */
    color: white;
    border: none;
    margin-right: 20px;
  }
  
  .save_btn:hover {
    background-color: #45a049; /* Slightly darker green */
  }
  
  /* Edit Button Specific Styling */
  .edit_btn {
    background-color: #2196f3; /* Blue for Edit */
    color: white;
    border: none;
  }
  
  .edit_btn:hover {
    background-color: #0b7dda; /* Slightly darker blue */
  }
  
/* App Description Styling */
.description {
    margin: 20px auto;
    text-align: center;
    font-size: 16px;
    color: #333;
    max-width: 80%;
    line-height: 1.5;
  }
  
